.nave{
    color:rgb(230, 220, 91);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    font-weight: bold;
}
.qq{
    background: linear-gradient(200deg,red,orange);
}
.qq h1{
    padding: 2%;color:rgb(245, 250, 248);
}
.qq input{
    height:8vh;
    width: 40%;font-size: 20px;
}
.qq button {
    height:5vh;
    width: 10%;
    margin: 3%;
    font-size: 20px;
    cursor: pointer;
    background-color: blueviolet;
    color: white;
    border: 0;
    border: 2px solid white;
    border-radius: 30px;
}
.qq button:hover{
    transform: scale(0.93)
}
.ww ul li:hover{
    transform: scale(0.93)
}
.ww ul {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    padding: 2%;
    font-size: 25px;
    background-color: blueviolet;
    color:white; cursor: pointer;
}
.ee{
    background: linear-gradient(200deg,red,orange);
    color: white;
    padding: 3%;
    height: 47vh;
}